/* popup STYLE */

.popup {
    position:relative;
    display:none;
   &__trigger{
       position:relative;
       padding-right: 1em;
   }
   &__menuItem{
        border-bottom: solid 1px cs('foreground-d');
        line-height: 2em;
        
   }
    &__content { 
        z-index:20; 
        position:absolute;
        left:50%;
        width: 180px;
        margin-left: -90px;
        padding: 0.2em 0;
        
        &:before {
            content: '';
            position: absolute;
            width: 0px;
            height: 0px; 
            top:-11px;
            left:50%;
            margin-left: -0.5em; 
            border: solid 7px transparent;
            border-bottom-color: cs('foreground-d2');;

        }
        &:after {
            content: '';
            position: absolute;
            width: 0px;
            height: 0px; 
            top:-10px; 
            left:50%;
            margin-left: -0.5em; 
            border: solid 7px transparent;
            border-bottom-color: cs('foreground');

        }

    }
            &__body{
            padding: 1.2em;
            background: cs('foreground');
           // box-shadow: 0px 0px 1px 1px cs('foreground-d2');
            border-radius: 3px;
            border: 1px solid cs('foreground-d2');;
        }
        
}
/* productInfo STYLE */

.block--inner{
    border-bottom: solid 4px #dadada;
    border-bottom-left-radius: 6px;
    border-bottom-right-radius: 6px;
}
.productInfo {
    .smartBonus{
         //text-align: center;
        padding:vr-block(.4) 0; 
    }
    .toOrder{
        width:80%;
        margin: vr-block(.4) auto vr-block(.1);
        display: block;
        @include for-size(tablet-l-up){
            width: 100%;
        }
    }
    &__content {
        .productCol__lock{
            text-align:left;
            width: 100%;
            display: inline-block;
            line-height: 1.7;
        }
        .productBadge{ 
            @include for-size(phone-only){
